<template>
  <div class="main-content container">
    <h2 class="main-content__title">
      {{content=='movie'?'Все фильмы':'Все сериалы'}}
    </h2>
    
    <div class="main-content__list">
      <div class="content-item" v-for="item in allMovies" :key="item">
        <img src="@/assets/images/No_Image.jpg" alt="" class="content-item__img" v-if="item.backdrop_path == null">
        <img :src="getImage + item.backdrop_path" alt="" class="content-item__img" v-else>
        <a href="#!" class="content-item__link">{{item.title}}</a>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex"
export default {
  props: ['content'],
  data() {
    return {
      page: 1,
      loading: false,
      allMovies: []
    }
  },
  computed: {
    ...mapGetters(['getImage'])
  },
  methods: {
    async getContent(){
      if(!this.loading){
        this.loading = true
        let action = this.content == 'movie' ? 'movies/getMovies' : 'tvSerials/getTv';
        await this.$store.dispatch(`${action}`, this.page)
        let getter = this.content == 'movie' ? 'movies/getMoviesArray' : 'tvSerials/getTvArray';
        const allInfo = this.$store.getters[`${getter}`]
        this.allMovies.push(...allInfo)
        this.loading = false
        this.page++
      }
    }
  },
  async created() {
    await this.getContent()
    window.addEventListener('scroll', () => {
      if(window.scrollY + window.innerHeight >= document.body.scrollHeight){
        this.getContent()
      }
    })
  },
}
</script>

