export default {
  namespaced: true,
  state: {
    path: 'https://api.themoviedb.org/3/search/multi',
    searchData: null
  },
  mutations: {
    getSearchData(state, payload){
      state.searchData = payload
    }
  },
  actions: {
    async getSearchData({commit, state, rootState}, search){
      try{
        const query = await fetch(`${state.path}?api_key=${rootState.apiKey}&language=ru-Ru&query=${search}&page=1&include_adult=false`)
        const res =  await query.json()
        const result = res.results
        console.log(result);
        commit('getSearchData', result)
      }catch(error){
        console.log(error);
      }
    }
  },
}