export default {
  namespaced: true,
  state: {
    TvPath: 'https://api.themoviedb.org/3/tv/popular',
    Tv: null
  },
  getters: {
    getTvArray(state){return state.Tv}
  },
  mutations: {
    getTv(state, payload){
      state.Tv = payload
    }
  },
  actions: {
    async getTv({commit, state, rootState}, page = 1){
      await fetch(`${state.TvPath}?api_key=${rootState.apiKey}&language=ru-Ru&page=${page}`)
      .then(response => response.json())
      .then(data => commit('getTv', data.results))
    }
  },
}