<template>
  <div class="content-item">
    <img src="@/assets/images/No_Image.jpg" alt="" class="content-item__img" v-if="item.backdrop_path == null">
    <img :src="getImage + item.backdrop_path" alt="" class="content-item__img" v-else>
    <a href="#!" class="content-item__link">{{item.title}}</a>
  </div>
</template>

<script>
export default {
  props: ['item'],
  computed: {
    getImage(){return this.$store.getters.getImage}
  },
}
</script>
