<template>
  <Transition name="upcoming__item">
    <div class="main-upcoming__item" v-if="slideView == index">
      <img :src="getImageFull + slide.backdrop_path" alt="" class="main-upcoming__item--img">
      <div class="main-upcoming__content container">
        <h1>{{slide.title}}</h1>
        <p>{{slide.overview}}</p>
        <BtnMore page="movie" :id="slide.id"/>
      </div>
      
      <div class="main-upcoming__next" @click="$emit('nextSlide')">
        <img :src="getImage + next.backdrop_path" alt="" class="main-upcoming__next--img">
        <div class="main-upcoming__next--text">
          <span>Следующий</span>
          <h4>{{next.title}}</h4>
        </div>
        <div class="main-upcoming__next--line"></div>
      </div>
    </div>
  </Transition>
</template>

<script>
import { mapGetters } from "vuex"
export default {
  props: ['index','slide', 'slideView', 'next'],
  computed: {
    ...mapGetters(['getImageFull', 'getImage'])
  }
}
</script>