<template>
  <div class="main-movies">
    <router-link to="/films" class="main-movies__link">
      Фильмы <fa icon="fa-solid fa-chevron-right" />
    </router-link>
    
    <Swiper
      :modules="modules"
      :breakpoints="swiperOptions.breakpoints"
      :space-between="24"
      :navigation="true"
    >
      <Swiper-slide 
        class="main-movies__item"
        v-for="(movi, index) in moviesList" 
        :key="index" 
        :movi="movi"
        @click="getMovie(movi, index)">
        <img src="@/assets/images/No_Image.jpg" alt="" class="main-movies__item--img" v-if="movi.backdrop_path == null">
        <img :src="getImage + movi.backdrop_path" alt="" class="main-movies__item--img" v-else>
      </Swiper-slide>
      <Swiper-slide
        class="main-movies__item">
        <router-link to="/films">
          <fa icon="fa-solid fa-chevron-right" />
          <span>Все фильмы</span>
        </router-link>
      </Swiper-slide>
    </Swiper>
    
    
    <InfoBlock
      :selectedId="selectedId"
      :index="index"
      :selectedMovie="selectedMovie"
      @close="selectedMovie = selectedId = null"
      page="movie"
    />
  </div>
</template>

<script>
import InfoBlock from "@/components/InfoBlock.vue"
import { mapActions, mapGetters, mapState } from "vuex"
import { Swiper, SwiperSlide } from 'swiper/vue'
import { Navigation} from 'swiper'
import 'swiper/scss';
import 'swiper/scss/navigation';

export default {
  components: {Swiper, SwiperSlide, InfoBlock},
  data() {
    return {
      moviesList: null,
      modules: [Navigation],
      swiperOptions: {
        breakpoints: {
          320: {slidesPerView: 1},
          576: {slidesPerView: 2},
          768: {slidesPerView: 3},
          992: {slidesPerView: 4},
          1200: {slidesPerView: 5}
        }
      },
      selectedId: null,
      selectedMovie: null,
      index: null
    }
  },
  methods: {
    ...mapActions('movies', ['getMovies']),
    ...mapActions('movieInfoBlock', ['getMovieById']),
    async getMovie(movie, ind){
      this.selectedId = movie.id
      this.selectedMovie = null
      this.index = ind
      await this.getMovieById(this.selectedId)
      this.selectedMovie = this.movie
    }
  },
  computed: {
    ...mapGetters('movies', ['getMoviesArray']),
    ...mapGetters(['getImageFull','getImage',]),
    ...mapState('movieInfoBlock', ['movie'])
  },
  async mounted() {
    await this.getMovies()
    this.moviesList = this.getMoviesArray
  },
}
</script>
