<template>
  <div class="main-rate">
    <h2 class="main-rate__title">Топ<span>10</span></h2>
  
    <Swiper
      :modules="modules"
      :breakpoints="swiperOptions.breakpoints"
      :space-between="24"
      :navigation="true">
      <Swiper-slide 
        class="main-rate__item"
        v-for="(item, index) in TopRateList" 
        :key="index" 
        :item="item">
        <div class="main-rate__item--info">
          <p>{{index+1}}</p>
        </div>
        <img :src="getImageFull + item.backdrop_path " alt="" class="main-rate__item--img">
      </Swiper-slide>
    </Swiper>
  </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from "vuex"
import { Swiper, SwiperSlide } from 'swiper/vue'
import { Navigation} from 'swiper'
import 'swiper/scss';
import 'swiper/scss/navigation';

export default {
  components: {Swiper, SwiperSlide},
  data() {
    return {
      TopRateList: null,
      modules: [Navigation],
      swiperOptions: {
        breakpoints: {
          768: {slidesPerView: 1},
          992: {slidesPerView: 2},
          1200: {slidesPerView: 3}
        }
      }
    }
  },
  methods: {
    ...mapActions('topRate', ['getTopRate'])
  },
  computed: {
    ...mapState('topRate', ['topRate']),
    ...mapGetters(['getImageFull','getImage'])
  },
  async mounted() {
    await this.getTopRate()
    this.TopRateList = this.topRate
  },
}
</script>
