<template>
  <div class="main-info" :class="{active: selectedId != null}">
    <fa icon="fa-solid fa-xmark" class="main-info__close" @click="$emit('close')" />
    <div class="main-info__block" v-if="selectedMovie">
      <img :src="getImageFull+selectedMovie.backdrop_path" alt="" class="main-info__block--img">
      <div class="main-info__content">
        <div class="main-info__content--text">
          <h2>{{selectedMovie.title}}</h2>
          <p>{{selectedMovie.overview}}</p>
          <ul class="main-info__content--tag">
            <li>{{new Date(selectedMovie.release_date).getFullYear()}},</li>
            <li v-for="el in selectedMovie.genres" :key="el.id">{{el.name}},</li>
            <li>2h 12m</li>
          </ul>
          <div class="main-info__content--actors">
            <Actors :type="page" :id="selectedId" :count="4"/>
          </div>
          <BtnMore :page="page" :id="selectedId"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex"
export default {
  props:['selectedId','index','selectedMovie', 'page'],
  computed: {...mapGetters(['getImageFull'])},
}
</script>
