export default {
  namespaced: true,
  state: {
    moviePath: 'https://api.themoviedb.org/3/',
    movieActors: [],
    tvActors: []
  },
  getters: {
    getCastMovie(state){return (count) => {
      return state.movieActors.slice(0, count)
    }},
    getCastTv(state){return (count) => {
      return state.tvActors.slice(0, count)
    }}
  },
  mutations: {
    getMovieActors(state, payload){
      state.movieActors = payload
    },
    getTvActors(state, payload){
      state.tvActors = payload
    },
  },
  actions: {
    async getActors({commit, state, rootState}, {id, type}){
      await fetch(`${state.moviePath}/${type}/${id}/credits?api_key=${rootState.apiKey}&language=ru-Ru`)
      .then(response => response.json())
      .then(data => {
        console.log(data.cast);
        if(type == 'movie'){
          commit('getMovieActors', data.cast)
        }else{
          commit('getTvActors', data.cast)
        }
      })
    }
  },
}