export default {
  namespaced: true,
  state: {
    upcoming: null,
    upcomingPath: 'https://api.themoviedb.org/3/movie/upcoming'
  },
  getters: {
    getUpcomingArray(state){
      return state.upcoming
    }
  },
  mutations: {
    getUpcoming(state, payload){
      state.upcoming = payload
    }
  },
  actions: {
    async getUpcoming({commit, state, rootState}){
      await fetch(`${state.upcomingPath}?api_key=${rootState.apiKey}&language=ru-Ru&page=1`)
      .then(response => response.json())
      .then(data => commit('getUpcoming', data.results))
    }
  },
}