export default {
  namespaced: true,
  state: {
    moviePath: 'https://api.themoviedb.org/3/movie/',
    movie: null
  },
  mutations: {
    getMovieById(state, payload){
      state.movie = payload
    }
  },
  actions: {
    async getMovieById({commit, state, rootState}, movieId){
      await fetch(`${state.moviePath}${movieId}?api_key=${rootState.apiKey}&language=ru-Ru`)
      .then(response => response.json())
      .then(data => commit('getMovieById', data))
    }
  },
}