export default {
  namespaced: true,
  state: {
    moviesPath: 'https://api.themoviedb.org/3/movie/popular',
    movies: null
  },
  getters: {
    getMoviesArray(state){return state.movies}
  },
  mutations: {
    getMovies(state, payload){
      state.movies = payload
    }
  },
  actions: {
    async getMovies({commit, state, rootState}, page = 1){
      await fetch(`${state.moviesPath}?api_key=${rootState.apiKey}&language=ru-Ru&page=${page}`)
      .then(response => response.json())
      .then(data => commit('getMovies', data.results))
    }
  },
}