export default {
  namespaced: true,
  state: {
    ratePath: 'https://api.themoviedb.org/3/movie/top_rated',
    topRate: null
  },
  getters: {},
  mutations: {
    getTopRate(state, payload){
      state.topRate = payload
    }
  },
  actions: {
    async getTopRate({commit, state, rootState}, page = 1){
      await fetch(`${state.ratePath}?api_key=${rootState.apiKey}&language=ru-Ru&page=${page}`)
      .then(response => response.json())
      .then(data => {
        const top10 = []
        data.results.forEach(item => {
          if(top10.length < 10){
            top10.push(item)
          }
        })
        commit('getTopRate', top10); 
      })
    }
  },
}