<template>
  <div class="actor" v-for="item in getCountLength" :key="item">
    <img :src="getImage + item.profile_path" alt="" class="actor-avatar">
    <span class="actor-name">{{item.name}}</span>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex"
export default {
  props: ['type', 'id', 'count'],
  methods: {
    ...mapActions('actors', ['getActors'])
  },
  computed: {
    getCountLength(){
      const cast = this.type == 'movie' ? 'getCastMovie' : 'getCastTv';
      console.log(this.$store.getters[`actors/${cast}`](this.count));
      return this.$store.getters[`actors/${cast}`](this.count)
    },
    ...mapGetters(['getImage'])
  },
  async created() {
    await this.getActors({id: this.id, type: this.type})
    this.getCountLength
  },
}
</script>

