<template>
  <div class="main-movies">
    <router-link to="/serials" class="main-movies__link">
      Сереалы <fa icon="fa-solid fa-chevron-right" />
    </router-link>
    
    <Swiper
      :modules="modules"
      :breakpoints="swiperOptions.breakpoints"
      :space-between="24"
      :navigation="true"
    >
      <Swiper-slide 
        class="main-movies__item"
        v-for="(movi, index) in TvSerialsList" 
        :key="index" 
        :movi="movi">
        <img src="@/assets/images/No_Image.jpg" alt="" class="main-movies__item--img" v-if="movi.backdrop_path == null">
        <img :src="getImage + movi.backdrop_path" alt="" class="main-movies__item--img" v-else>
      </Swiper-slide>
      <Swiper-slide
        class="main-movies__item">
        <router-link to="/serials">
          <fa icon="fa-solid fa-chevron-right" />
          <span>Все сериалы</span>
        </router-link>
      </Swiper-slide>
    </Swiper>
    
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex"
import { Swiper, SwiperSlide } from 'swiper/vue'
import { Navigation} from 'swiper'
import 'swiper/scss';
import 'swiper/scss/navigation';

export default {
  components: {Swiper, SwiperSlide},
  data() {
    return {
      TvSerialsList: null,
      modules: [Navigation],
      swiperOptions: {
        breakpoints: {
          320: {slidesPerView: 1},
          576: {slidesPerView: 2},
          768: {slidesPerView: 3},
          992: {slidesPerView: 4},
          1200: {slidesPerView: 5}
        }
      }
    }
  },
  methods: {
    ...mapActions('tvSerials', ['getTv'])
  },
  computed: {
    ...mapGetters('tvSerials', ['getTvArray']),
    ...mapGetters(['getImageFull','getImage',])
  },
  async mounted() {
    await this.getTv()
    this.TvSerialsList = this.getTvArray
  },
}
</script>
